.publishing-a-story__wrapper {
  background-color: $basic-extra-dark;
  background-image: url("../img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-position: top;
}

.publishing-a-story__title {
  font-size: 24px;
  line-height: 33px;
  color: $basic-white;
  padding: 93px 20px 45px;
  margin: 0;
}

.publishing-a-story__slogan {
  background-color: $basic-white;
  font-weight: 400;
  line-height: 30px;
  padding: 44px 20px 15px;
  margin: 0;
}
